.mainpage{
  width: 100%;
  background-color: rgba(247,247,247,1);
}

.bottom {
  height: auto;
  width: 100%;
  overflow: hidden;
  position: relative;
  margin-top: 30px;
  margin-bottom: 0px;
}

.navigation {
  position: relative;
  padding-top: 3em;
  width: 90%;
  left: 5%;
}
.navigationTab {
  margin-top: 20px;
}

.navigationTab:hover {
  opacity: 50%;
}

.headerHomePage{
  width: 100%;
  background-color: rgba(247,247,247,1);
}

.homePageTitle{
  top: 10px;
  margin-left: 12%;
  position: relative;
  white-space: nowrap;
  text-align: left;
  font-family: Brandon Grotesque;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  color: rgba(38,38,38,1);
  letter-spacing: 3px;
}

.question{
  margin-left: 10%;
  position: relative;
  white-space: nowrap;
  text-align: left;
  font-family: 'Times New Roman', Times, serif;
  font-style: normal;
  font-weight: bold;
  font-size: 1.3em;
  color: #707070;
  letter-spacing: 1px;
  padding-bottom: 0.5em;
}

.questionNumber {
  top: 1em;
  margin-left: 10%;
  position: relative;
  white-space: nowrap;
  text-align: left;
  font-family: 'Times New Roman', Times, serif;
  font-style: normal;
  font-size: 1.2em;
  color: #707070;
  letter-spacing: 1px;
}

.done {
  position: relative;
  width: 80%;
  left:10%;
  padding: 1em;
}

.doneMark {
  position: relative;
  width: 20%;
  left: 40%;
}

.doneMessage{
  top: 1em;
  position: relative;
  text-align: left;
  font-family: 'Times New Roman', Times, serif;
  font-style: normal;
  font-size: 1.3em;
  color: #707070;
  letter-spacing: 1px;
  padding-bottom: 0.5em;
}

.option {
  position: relative;
  width: 80%;
  left: 10%;
  border: #535252 1px solid;
  padding: 0.5em;
}
.option:hover {
  border: #16abf0 1px solid;
  background-color: rgba(0, 110, 255, 0.356);
}

.optiontrue:active {
  opacity: 70%;
  background-color: rgba(0, 255, 85, 0.356);
}

.optionfalse:active {
  opacity: 70%;
  background-color: rgba(255, 0, 0, 0.356);
}

.hintMessage {
  position: relative;
  width: 80%;
  left: 10%;
  background-color: rgba(253, 145, 145, 0.5);
  min-height: 4em;
  height: auto;
  border-radius: 10px;
  margin-bottom: 2em;
}

.hintMessageText {
  color: red;
  position: relative;
  padding-top:1em;
  width:90%;
  left: 5%;
}

.correctMessage {
  position: relative;
  width: 80%;
  left: 10%;
  background-color: rgba(145, 253, 154, 0.5);
  min-height: 4em;
  height: auto;
  border-radius: 10px;
  margin-bottom: 2em;
}

.correctMessageText {
  color: green;
  position: relative;
  padding-top:1em;
  width:90%;
  left: 5%;
}

.normalText{
  top: 30px;
  margin-left: 12%;
  position: relative;
  text-align: left;
  text-align: left;
  font-family: Tisa OT;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  color: rgba(38,38,38,1);
  letter-spacing: 0.24px;
}

.firtsParagraph {
  width: 60%;
  margin-top: -12em;
}

.secondParagraph {
  margin-top: 3em;
  width: 80%;
}

.secondHomePageTitle{
  top: 40px;
  margin-left: 12%;
  position: relative;
  white-space: nowrap;
  text-align: left;
  font-family: Tisa OT;
  font-style: normal;
  font-weight: normal;
  font-size: 33px;
  color: rgba(38,38,38,1);
}

.homePageText{
  margin-left: 13%;
  top: 50px;
  position: relative;
  text-align: left;
  font-family: Tisa OT;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  color: rgba(38,38,38,1);
  width: 44%;
}

.globe{
  opacity: 20%;
  position: relative;
  top:-440px;
  left: 500px;
}


.globeLesson {
  opacity: 20%;
  position: fixed;
  max-width: 50%;
  left: 25%;
  height: auto;
  margin-top: -6em;
}

.body{
  position: relative;
  background-color: #FFFFFF;
  margin-top: -350px;
  width: 90%;
  left: 5%;
  padding-bottom: 10px;
  height: auto;
}
.bodyLesson{
  position: relative;
  background-color: #FFFFFF;
  margin-top: 5em;
  width: 90%;
  left: 5%;
  padding-bottom: 10px;
  min-height: 400px;
}

.video{
  width: 90%;
  margin-left: 5%;
  padding-top: 6em;
  height: 668px;
  padding-bottom: 5em;
}

.globe1 {
  position: relative;
  left: 73%;
}

.appScreen {
  position: relative;
  height: auto;
  width: 80%;
  left: 10%;
}

.yesButton {
  max-width: 250px;
}

.yesButton:hover {
  opacity: 50%;
}

.yesButton:active {
  opacity: 100%;
}

.mailButton {
  max-width: 230px;
}

.mailButton:hover {
  opacity: 50%;
}

.mailButton:active {
  opacity: 100%;
}

.buttons {
  position: relative;
  width: 50%;
  left: 25%;
  padding-top: 2em;
  padding-bottom: 2em;
}

.mail {
  position: relative;
  width: 80%;
  left: 10%;
  border: #535252 2px solid;
  border-radius: 2em;
  height: auto;
}

.mailTile {
  position: relative;
  font-family: 'Times New Roman', Times, serif;
  color: #707070;
  font-size: 1.5em;
  font-weight: bold;
  left: 5%;
  margin-top: 1em;
}

.mailText {
  position: relative;
  font-family: 'Times New Roman', Times, serif;
  color: #707070;
  font-size: 1.2em;
  left: 5%;
  width: 80%;
}

.hide {
  display: none;
}


@media only screen and (max-width: 1400px) {
  .yesButton {
    max-width: 200px;
  }

  .mailButton {
    max-width: 190px;
  }

  .globe {
    display: none;
  }
  .globe1 {
    display: none;
  }
  .homePageText {
    width: 80%;
  }

  .headerHomePage {
    padding-bottom: 70px;
  }

  .body{
    margin-top: 0px;
  }

  .firtsParagraph {
    width: 80%;
    margin-top: 0px;
  }

  .bodyLesson{
    margin-top: 3em;
  }

  .globeLesson {
    max-width: 70%;
    left: 15%;
  }
}

.startButton {
  position: relative;
  margin-left: -10px;
  margin-top: 40px;
}

.startButton:hover {
  opacity: 40%;
}



.naslovaktivnost {
  font-size: 1.2em;
  color: #535252;
  text-align-last: center;
}

.navbar{
  width: 100%;
  background-color: rgba(247,247,247,1);
}

.textlink{  
  white-space: nowrap;
  text-align: left;
  font-family: Brandon Grotesque;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  color: rgba(0,0,0,1);
  letter-spacing: 1px;
  margin: 20px;
}

.naslov{

  font-size: 2em;
  color: #535252;
  padding: 0.5em;
  margin: 0px;
  margin-left: 2.5%;
  margin-top:0.3em;
  width: 95%;

}